/* Global CSS*/

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: "Roboto", sans-serif;
  -moz-osx-font-smoothing: "Roboto", sans-serif;
  font-style: normal;
  background-color: var(--black);
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

:root {
  --primary: #b9373b;
  --secondary: #d54a58;
  --tertiary: #eb5966;
  --gold: #eace61c3;
  --initial-gold: #ffdc73c3;
  --header-color: #f5f5f5;
  --black: #000000;
  --white: #ffffff;
  --github: #7a7786;
  --bg-shade: #7c847e;
  --dark-shadow: #464b4e
}

.btn{
  font-family: "Roboto";
  display: inline-block;
  padding: 14px 32px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  border-radius: 4px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  transition: background-color 0.5s;
  -webkit-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  -ms-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
}
.btn-outline-primary{
  color: var(--primary);
  background-color: var(--white);
  border: 1px solid var(--primary)
}

.btn-outline-primary:hover {
  color: var(--white);
  background-color: var(--primary);
}

.btn-primary{
  color: var(--white);
  background-color: var(--primary);
  border: 1px solid var(--primary)
}

.btn-primary:hover {
  color: var(--primary);
  background-color: var(--white);
}

.btn-github {
  color: var(--white);
  background-color: var(--github);
  border: var(--github);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 2ps 0 rgba(16,24,40,0.95);
  gap: 16px;
}

.btn-github:hover {
  color: var(--github);
  background-color: var(--white);
}

/* Global CSS Ends*/

/* Main Headings*/

/* Heading 1*/
h1 {
  font-size: 56px;
  font-weight: 700;
  line-height: 67px;
  color: var(--white);
}

/* Heading 2*/
h2 {
  font-size: 36px;
  font-weight: 700;
  line-height: 58px;
  color: var(--primary);
}

/* Heading 3*/
h3 {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
  color: var(--secondary);
}

/* Paragraph text large medium small*/
/* Body 1*/
.text-lg{
  color: var(--gold);
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 27px;
}
/* Body 2*/
.text-md{
  color: var(--gold);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}
/* Body 3*/
.text-sm{
  color: var(--bg-shade);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

/* Section Titles*/
/* Skills & About Me */
.section--title{
  color: var(--header-color);
  font-size: 21px;
  text-align: center;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  display: flex;
  align-items: flex-start;
}

/* Portfolio, Testimonial & Contact Me */
.sub--title {
  color: var(--header-color);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}

/* Section Titles Ends */

/* Navbar Style Start */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 85.333px;
  background: var(--black);
  box-shadow: 0px 5.333px 80px 0 #464b4e; 
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.navbar--items > ul{
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
}
/* Navbar Content */
.navbar--content{
  color: var(--tertiary);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
}
.navbar--active-content{
  color: var(--gold);
}

/* Main Section Style */
.main--section{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 133.333px 85.333px 133.333px;
  align-items: center;
  justify-content: space-between;
  gap: 32px;
  background-color: var(--black);
}
.main--section--content--box{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
}

.main--section--content{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 21.333px;
}

.main--section--content--box > button{
  margin-top: 21.333px;
}
.main--section--title{
  color: var(--header-color);
  font-size: 74.667px;
  font-weight: 700;
  line-height: 90px;
  align-self: stretch;
}
.main--section--title--color{
  color: var(--primary);
}
.main--section--description{
  color: var(--gold);
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}
.main--section--img{
  display: flex;
}
.main--section--img >img {
  width: 100%;
  height: 100%
}

/* Main Section Style Ends */

/* Experience Section Style */
.experience--section{
  display:flex;
  padding: 149.333px 85.333px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 106.667px;
  background-color: var(--black);
}
.experience--section--heading{
  color: var(--header-color);
  font-size: 64px;
  font-weight: 700;
  line-height: 77px;
}
.experience--section--subheading{
  font-size: 16px;
}
.experience--section--container{
  display: grid;
  justify-content: center;
  align-items: flex-start;
  gap: 42.6px;
  grid-template-columns: repeat(5, minmax(250px, 1fr));
}
.experiences--section--card {
  display: flex;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  flex: 1 0 0;
  border-radius: 10.6px;
  background-color: var(--white);
  flex-basis: 0;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden; /* Ensure content doesn’t overflow the card */
  overflow-x:auto;
}
.experience--section--card:hover{
  border-bottom: 4px solid var(--primary);
}
.experience--section--img {
  display: flex;
  padding: 13.3px;
  justify-content: center;
  align-items: center;
  gap: 13.3px;
  border-radius: 10.6px;
  background-color: var(--white);
  box-shadow: 0 0 16px 0 rgba(0,0,0,0.1);
}
.experience--section--card--content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  align-self: stretch;
  flex-grow: 1;
  overflow: auto; /* Enable scrolling within the content container */
  max-height: 600px; /* Set a maximum height for the content to limit its size */
}
.experience--section--title {
  color: var(--primary);
  font-size: 32px;
  font-weight: 700;
  line-height: 45px;
}
/*  Experience Style Ends */

/* About Me */
.about--section{
  display: grid;
  padding: 133.333px 85.333px;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  gap: 114.6px;
  background-color: var(--black);
}

.about--section--img > img{
  width: 100%;
  height: 100%;
}

/* About Me Ends */

/* Portfolio section */
.portfolio--section{
  display: flex;
  padding: 112px 85px;
  flex-direction: column;
  align-items: flex-start;
  gap: 80px;
}

.portfolio--container-box{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%
}
.portfolio--container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 21.3px
}
.portfolio--section--container{
  display: flex;
  align-items: top;
  justify-content: center;
  width: 100%;
  gap: 42.6px;
}
.portfolio--section--img{
  border-radius: 8px;
  width: 100%;
}
.portfolio--section--img > img{
  width: 100%;
}
.portfolio--section--card{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  flex: 1 0 0;
  background: var(--black);
  /*box-shadow: 0 0 32px 0 var(--dark-shadow);*/
  cursor: pointer;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  border: 1.33px solid var(--dark-shadow);
}
.portfolio--section--card--content {
  display: flex;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  align-self: stretch;
}

.portfolio--link {
  text-decoration: none;
  color: var(--white);
  display: flex;
  gap: 16px;
  border-bottom: 1.33px solid var(--primary);
  padding-bottom: 10.6px;
  font-weight: 600;
}
.portfolio--section--card:hover path {
  stroke: var(--primary);
}
/* Portfolio Ends */

/* Footer Starts */
.footer--container {
  display: flex;
  padding: 106.667px 85.333px;
  flex-direction: column;
  align-items: flex-start;
  background: var(--gold);
  align-items: center;
  align-self: stretch;
}
.footer--link--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}
.footer--items > ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
}
.footer--items ul > li > a {
  text-decoration: none;
  color: var(--black)
}
.footer--social--icon > ul {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 16px;
  flex-shrink: 0;
  text-decoration: none;
}
.divider {
  margin: 106px 0 42.67px;
  height: 1.333px;
  width: 100%;
  background: rgba(40, 41, 56, 0.55);
}
.footer--content--container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}
.footer--content {
  color: var(--black);
  font-size: 18.667px;
  font-weight: 400;
  line-height: 28px;
}

/* Responsive Screens */
@media only screen and (max-width: 1800px) {
  .main--section--title {
    font-size: 68px;
    line-height: 70px;
  }
  .experience--section--title {
    font-size: 28px;
    line-height: 40px;
  }
}

@media only screen and (max-width: 1600px) {
  .experience--section--heading {
    font-size: 54px;
    line-height: 70px;
  }
  .experience--section--container {
    gap: 16px;
  }
  .experience--section--card {
    gap: 28px;
  }
  .experience--section--card--content {
    gap: 20px;
  }
  .experience--section--title {
    font-size: 23px;
    line-height: 30px;
  }
  .experience--section--description {
    font-size: 17.333px;
    line-height: 27px;
  }
}

@media only screen and (max-width: 1200px) {
  .btn-outline-primary {
    display: none;
  }
  .main--section {
    display: flex;
    flex-direction: column-reverse;
    padding-bottom: 70px;
  }
  .main--section--title,
  .main--section-description,
  .footer--content {
    text-align: center;
  }
  .experience--section--container,
  .about--section,
  .portfolio--section--container,
  .portfolio--container-box,
  .footer--link--container,
  .footer--items > ul,
  .footer--content--container {
    display: flex;
    flex-direction: column;
  }
  .experience--section,
  .portfolio--container,
  .main--section--content,
  .main--section--content--box {
    align-items: center;
  }
  .portfolio--container-box {
    gap: 30px;
  }
  .experience--section,
  .testimonial--section,
  .contact--section,
  .footer--container {
    gap: 20px;
    padding-top: 70px;
    padding-bottom: 70px;
  }
  .about--section {
    gap: 20px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .testimonial--section--card {
    padding: 25px;
  }
  .main--section--title,
  .experience--section--heading,
  .sections--heading {
    font-size: 35px;
    line-height: 40px;
  }
  .main--section--content--box {
    gap: 10px;
  }
  .contact--form--container {
    width: 100%;
  }
  .container {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .footer--social--icon > ul,
  .footer--items > ul {
    padding: 0;
    align-items: center;
  }
  .divider {
    margin: 20px;
  }
}

/* Hamburger menu  */

.nav__hamburger {
  display: none;
  width: 1.875rem;
  height: 1.313rem;
  flex-direction: column;
  justify-content: space-around;
  position: absolute;
  top: 25px;
  right: 25px;
}

.nav__hamburger__line {
  display: block;
  height: 0.188rem;
  width: 100%;
  background-color: var(--white);
  border-radius: 0.625rem;
  transition: all ease-in-out 0.2s;
}

@media screen and (max-width: 1200px) {
  .nav__hamburger {
    display: flex;
    z-index: 200;
  }

  .navbar--items {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--black);
    top: -22rem;
    left: 0;
    width: 100%;
    transition: all ease-in-out 0.4s;
  }
  .navbar--items ul {
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 3rem 0 0.6rem;
    z-index: -100;
  }
  .navbar--items ul li {
    text-align: center;
  }
  .navbar--items ul li a {
    padding: 0.5rem;
  }
  .navbar--items {
    z-index: -1000;
  }
  .navbar--items.active {
    display: flex;
    top: 30px;
  }
  .nav__hamburger.active :nth-child(1) {
    transform: rotate(45deg) translate(0.45rem, 0.1875rem);
  }

  .nav__hamburger.active :nth-child(2) {
    opacity: 0;
  }

  .nav__hamburger.active :nth-child(3) {
    transform: rotate(-45deg) translate(0.45rem, -0.1875rem);
  }
}
/* Responsive Screens Ends*/